<script>
// @ is an alias to /src
import HomeCard from '../components/HomeCard.vue'
export default {
  name: 'Home',
  components: {
      HomeCard
  },
  data(){
      return{
          cards: [
              {img: 'about.png', title: 'About Me', body: 'On About Me page you can find more info about me!', url:'/about'},
              {img: 'projects.png', title: 'Projects', body: 'On this page you can find my projects',url:'/projects'},
              {img: 'contact.png', title: 'Contact', body: 'On this page you can find contact info.',url:'/contact'},
          ],
          tempString: "",
          i: 0
      }
  },
  methods: {
      typing(){
          let fullString = "a Web Developer";
          if(this.i < fullString.length){
              this.tempString += fullString.charAt(this.i);
              this.i++;
              setTimeout(this.typing, 200);
          }
      }
  },
  mounted(){
      this.typing();
  }
}
</script>

